import * as React from "react";

import Box from '@mui/material/Box';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
 
export const BasicSelect = ({ schedules, schedule }) => {

   if (!schedules) return
   const [scheduleFilter, setScheduleFilter] = React.useState(schedules[0].title || '')

   const handleChange = ({ target }) => {
      setScheduleFilter(target.value);
   }

   return (
      <Box sx={{ minWidth: 120 }}>
         <FormControl fullWidth>
            <InputLabel id="demo-simple-select-label">Schedule</InputLabel>
            <Select
               labelId="demo-simple-select-label"
               id="demo-simple-select"
               value={scheduleFilter}
               label="Schedule"
               onChange={handleChange}
               sx={{fontSize: 14, height: 35, width: 235}}
            >
               {schedules && schedules.map(schedule => <MenuItem sx={{fontSize: 12,}} key={schedule._id} value={schedule.title} >{schedule.title}</MenuItem>)}
            </Select>
         </FormControl>
      </Box>
   );
}