import React, { useRef, useEffect, useState } from 'react'

import { utilService } from '../services/util.service'

export const SchedulePreviewHour = ({ hour, idx, earlierTaskHour }) => {

   const scollToRef = useRef();
   const [isCurrentHour, setIsCurrentHour] = useState(false)

   useEffect(() => {
      // if (idx === utilService.getHourNum()) scollToRef.current.scrollIntoView()
      if (idx === earlierTaskHour - 3) scollToRef.current.scrollIntoView()
      if (idx === utilService.getHourNum()) setIsCurrentHour(true)

   }, [earlierTaskHour])

   const getCurrentHour = () => {
      utilService.getHourNum()
   }

   return (
      <li className='schedule-preview-hour' ref={scollToRef}
         style={{
            gridRow: `${idx + 1}/${idx + 1}`,
            gridColumn: `${1}/${1}`,
            color: `${ isCurrentHour && 'red'}`,
            borderBottom: `${ isCurrentHour && '1px solid red '}`
         }}>
         {hour.hour}
      </li>
   )
}