import React from 'react'

import { BasicSelect } from '../cmps/mui-templats/select'

export const ScheduleFilter = ({ schedules, schedule }) => {

   const displayDate = () => {
      const event = new Date(Date.now());
      const options = { weekday: 'long', year: 'numeric', month: 'numeric', day: 'numeric', hour: 'numeric', minute: 'numeric' };
      return event.toLocaleDateString('en-GB', options)
   }

   return (
      <main className='schedule-filter'>
         <div className="board-container">
            <BasicSelect className="board" schedules={schedules} schedule={schedule} />
            <div className="add-board">New Board</div>
         </div>
         
         <div className="date">{displayDate()}</div>
      </main>
   )
}